/*
==========================================================================
    Base styles
==========================================================================
*/

* {
    box-sizing: border-box !important;
    padding: 0px;
    margin: 0px;
}

*:focus {
    outline: none !important;
}

html {
    font-size: $font-size-base;
}

body {
    position: relative;
    padding: 0px;
    margin: 0px;
    background-color: $col-body-bg;
    font-family: 'Montserrat', helvetica, arial, sans-serif;
    font-size: $font-size;
    -webkit-font-smoothing: antialiased;
}

a,
img,
a img {
    border: none;
}

a {
    color: $col-tert;
    text-decoration: none !important;
    transition: all .15s;
}

a:hover,
a:focus,
a:active {
    color: $col-sec;
}

p {
    color: $col-text;
    font-size: 1em;
    line-height: 1.7em;

    &.caption {
        color: #999;
        font-size: .875em;
    }
}

hr {
    display: block;
    clear: both;
    width: 100%;
    margin: 20px auto;
    padding: 0;
    height: 0;
    border-top: 1px solid #eee;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
    font-family: 'Roboto Slab', georgia, times, serif;
    color: $col-text;
    line-height: 1.3em;
    font-weight: 300;
}

.clear {
    display: block;
    width: 100%;
    clear: both;
    height: 0px;
}

.text-justify {
    text-align: justify;
}

.text-left {
    text-align: left;
}

.text-right {
    text-align: right;
}

.text-center {
    text-align: center;
}

.text-nowrap {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.img_responsive {
    max-width: 100%;
}

.container {

}

.btn {
    appearance: none;
    text-decoration: none;
    display: inline-block;
    background: none;
    border: none;
    text-align: center;
    height: 40px;
    padding: 0 15px;
    border-radius: $border-rad-inner;
    border: 2px solid transparent;
    border-bottom-color: rgba( #000, .2 );
    color: #fff;
    font-size: .875rem;
    line-height: 36px;
    font-weight: 700;
    transition: all 0.15s ease-in-out;

    &:hover,
    &:focus {
        padding: 0 12px 0 18px;
        border-color: transparent;
        color: #fff;
    }

    &.btn-disabled {
        border-color: $col-grey-dark;
        background-color: $col-grey-light;
        color: $col-text-light;
        font-weight: normal;
        cursor: not-allowed;

        &.btn-small {
            border-width: 1px;
            line-height: 28px;
        }

        &:hover {
            padding: 0 15px;

            &.btn-small {
                padding: 0 10px;
            }
        }
    }

    &.btn-primary {
        background-color: $col-tert;

        &:hover,
        &:focus {
            background-color: tint( $col-tert, 20% );
        }
    }

    &.btn-secondary {
        background-color: $col-sec;

        &:hover,
        &:focus {
            background-color: tint( $col-sec, 20% );
        }
    }

    &.btn-tertiary {
        background-color: $col-prim;

        &:hover,
        &:focus {
            background-color: tint( $col-prim, 20% );
        }
    }

    &.btn-white {
        background-color: #fff;
        color: $col-text;
        font-weight: normal;

        &:hover,
        &:focus {
            background-color: $col-grey;
        }
    }

    &.btn-default {
        border-color: $col-grey-dark;
        background-color: #fff;
        color: $col-text;
        font-weight: normal;

        &.btn-small {
            border-width: 1px;
            line-height: 28px;
        }

        &:hover,
        &:focus {
            border-color: $col-grey-darker;
        }
    }

    &.btn-ghost {
        border-color: #fff;
        background: transparent;
        color: #fff;
        line-height: 36px;
        font-weight: normal;

        &:hover,
        &:focus {
            border-color: transparent;
            background-color: $col-grey;
            color: $col-text;
        }
    }

    &.btn-small {
        height: 30px;
        padding: 0 10px;
        line-height: 26px;
        font-weight: normal;
        font-size: .75rem;

        i {
            font-size: 14px;
        }

        &:hover,
        &:focus {
            padding: 0 7px 0 13px;
        }

        &.btn-ghost {
            border-width: 1px;
        }
    }

    &.btn-big {
        height: 50px;
        padding: 0 20px;
        font-size: 1rem;
        line-height: 50px;
    }

    &.btn-full {
        width: 100%;
    }
}

.fa-small {
    font-size: 0.75em;
}

.fa-left {
    margin-right: 6px;
}

.fa-right {
    margin-left: 6px;
}

.fa-center {
    margin: 0 6px;
}

.fa-prim {
    color: $col-prim;
}

.fa-sec {
    color: $col-sec;
}

.fa-tert {
    color: $col-tert;
}

.contentwrapper {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-weight: 700;

        &:first-child {
            margin-top: 0;
        }
    }

    h1 {
        font-size: $font-size-xxl;
        margin: 25px 0 15px;
    }

    h2 {
        font-size: $font-size-xxl;
        margin: 25px 0 15px;
    }

    h3 {
        font-size: $font-size-xl;
        margin: 20px 0 10px;
    }

    h4 {
        font-size: $font-size-xl;
        margin: 20px 0 10px;
    }

    h5,
    h6 {
        font-size: $font-size-lg;
        font-size: 1em;
        margin: 15px 0 5px;
    }

    p {
        margin: 15px 0;

        &:first-child {
            margin-top: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }

        strong {

        }

        em {
            color: $col-text-light;
        }

        a {
            text-decoration: underline;

            &:hover {
                text-decoration: none;
            }
        }

        .btn {
            margin: 10px 5px 0;
            text-decoration: none !important;

            &:first-child {
                margin-left: 0;
            }

            &:last-child {
                margin-right: 0;
            }

            &.btn-full {
                margin-left: 0;
                margin-right: 0;
            }
        }

        &.intro {

        }

        &.micro {
            font-size: $font-size-sm;
            color: $col-text-light;
        }
    }

    hr {

    }

    ul,
    ol {
        margin: 20px 0 20px 25px;

        li {
            color: $col-text;
            font-size: $font-size;
            line-height: 1.5em;
            padding: 3px 0;
        }
    }

    blockquote {
        border-left: 3px solid $col-grey;
        margin: 30px 0;
        padding: 10px 0 10px 30px;
        font-style: italic;
        font-size: $font-size-xl;
        line-height: 1.5em;
        font-family: 'Gloria Hallelujah';
        color: $col-sec;

        &:before, &:after {
            content: '"';
        }

        &:before {
            margin-right: 5px;
        }

        &:after {
            margin-left: 5px;
        }
    }

    .intro {
        font-size: 1.125em;
        color: #666;
    }

    .content-highlight {
        display: block;
        margin: 20px -20px;
        padding: 20px;
        border-radius: $border-rad-inner;
        background-color: tint( $col-tert, 90% );

        &:first-child {
            margin-top: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &.columns {
        column-count: 2;
        column-gap: 60px;
    }
}

.spacer {
    display: block;
    clear: both;
    width: 100%;

    &.spacer10 {
        height: 10px;
    }

    &.spacer20 {
        height: 20px;
    }

    &.spacer30 {
        height: 30px;
    }

    &.spacer40 {
        height: 40px;
    }

    &.spacer50 {
        height: 50px;
    }

    &.spacer100 {
        height: 100px;
    }

    &.spacer250 {
        height: 250px;
    }

    &.spacer500 {
        height: 500px;
    }
}

.nomargin {
    margin: 0 !important;
}

.nopadding {
    padding: 0 !important;
}

.imgwrap {
    display: block;
    position: relative;
    width: 100%;
    overflow: hidden;

     img {
        width: 100%;
        height: inherit;
        object-fit: cover;
        vertical-align: top;
    }
}

.img_overlay {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: all .15s;

    &.img_overlay_color {
        z-index: 5;
        background-color: #347cab;
        opacity: .5;
    }

    &.img_overlay_gradient {
        z-index: 5;
        background: linear-gradient(to bottom, rgba( #000, 0 ), rgba( #000, .5 ));
    }
}

.sectiontitle {
    margin: 15px 0 30px;
    text-align: center;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }

    .title {
        color: $col-prim;
        font-size: $font-size-hero;
    }

    .intro {
        margin: 10px 0 0;
        font-family: 'Gloria Hallelujah', cursive;
        color: $col-sec;
        font-size: $font-size-xl;
    }
}

.section {
    position: relative;
    padding: 50px 0;
    background-color: #fff;

    &.border-top {
        border-top: 2px solid $col-tert;
    }

    &.bg-grey {
        background-color: $col-grey-light;
    }

    &.bg-blue {
        background-color: tint( $col-prim, 95% );

        .contentwrapper {

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                color: $col-prim;
            }
        }
    }

    &.bg-orange {
        background-color: tint( $col-tert, 90% );

        .sectiontitle {

        }

        .contentwrapper {

            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                color: $col-tert;
            }
        }
    }

    &.bg-blue-fade {
        background: linear-gradient(to bottom, rgba( #fff, 0 ), tint( $col-prim, 95% ));
    }

    &.bg-blue-fade-inverted {
        background: linear-gradient(to top, rgba( #fff, 0 ), tint( $col-prim, 95% ));
    }

    &.bg-orange-fade {
        background: linear-gradient(to bottom, rgba( #fff, 0 ), tint( $col-tert, 90% ));
    }

    &.bg-orange-fade-inverted {
        background: linear-gradient(to top, rgba( #fff, 0 ), tint( $col-tert, 90% ));
    }

    &.section-has-background {

        .section-background {
            position: absolute;
            z-index: 1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #fff;

            .container {
                position: relative;
                padding: 0;
                height: 100%;
            }

            .section-background-face {
                position: absolute;
                z-index: 1;
                width: 100%;
                height: 100%;
                background-repeat: no-repeat;
                background-position: 90% 0;
                background-size: auto 140%;
            }
        }

        & > .container {
            position: relative;
            z-index: 2;
        }
    }
}

.col-succes {
    color: $col-succes;
}

.col-warning {
    color: $col-warning;
}

.col-error {
    color: $col-error;
}

.fixedheight {
    position: relative;
    overflow: hidden;

    .fade {
        position: absolute;
        z-index: 1;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 50px;
        background: linear-gradient(to bottom, rgba(#fff,0), rgba(#fff,1));
    }
}

.slick-list,
.slick-track {
    height: 100%;
}

.display-flex {
    display: flex;
    align-items: center;
}

.display-block {
    display: block;
}

.display-inline {
    display: inline;
}

.display-inline-block {
    display: inline-block;
}