/*
==========================================================================
    Variables
==========================================================================
*/

$col-prim: #b3ba54;
$col-sec: #ee7b49;
$col-tert: #385780;

$col-prim-darker: darken( $col-prim, 5% );
$col-sec-darker: darken( $col-sec, 5% );
$col-tert-darker: darken( $col-tert, 5% );

$col-prim-lighter: lighten( $col-prim, 10% );
$col-sec-lighter: lighten( $col-sec, 5% );
$col-tert-lighter: lighten( $col-tert, 5% );

$col-body-bg: #fff;

$col-text: #666;
$col-text-dark: #333;
$col-text-light: #999;

$col-grey: #eee;
$col-grey-dark: #ddd;
$col-grey-darker: #aaa;
$col-grey-light: #f9f9f9;

$border-rad-inner: 3px;
$border-rad-outer: 5px;

$font-size-base: 16px;
$font-size-xxs: .5em;
$font-size-xs: .75em;
$font-size-sm: .875em;
$font-size: 1em;
$font-size-md: 1.125em;
$font-size-lg: 1.25em;
$font-size-xl: 1.5em;
$font-size-xxl: 2em;
$font-size-hero: 2.5em;
$font-size-hero-lg: 3em;
$font-size-hero-xl: 3.5em;
$font-size-hero-xxl: 4em;

$col-error: #FF6961;
$col-warning: #FFB347;
$col-succes: #77DD77;

$col-twitter: #55acee;
$col-facebook: #3b5998;
$col-linkedin: #007bb5;
$col-google: #dd4b39;
$col-youtube: #bb0000;
$col-instagram: #125688;
$col-whatsapp: #4dc247;
$col-pinterest: #cb2027;
$col-dropbox: #007ee5;